import React from 'react';
import styled from 'styled-components';
import CardCap from '../images/image_cap.png';

const StyledCard = styled.div`
  background: inherit;
  box-shadow: rgba(25, 17, 34, 0.05) 0px 3px 10px;
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-radius: 0.25rem;

  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1) 0s,
    box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0s,
    padding 250ms cubic-bezier(0.4, 0, 0.2, 1) 0s;

  transition: 0.3s;

  :hover {
    transform: translateY(-4px);
    box-shadow: rgba(25, 17, 34, 0.1) 0px 10px 42px;
    background: linear-gradient(
      to right,
      #f09819,
      #ff512f
    ); 
  }
`;


const Card = ({ title, children }) => (

  <StyledCard class="card">
    <img className="card-img-top" src={CardCap} alt="Card cap" />
    <div className="card-body">
      <h5 className="card-title">{title}</h5>
      <p className="card-text">{children}</p>
    </div>
    <div className="card-footer">
      <small className="text-muted">Last updated 3 mins ago</small>
    </div>
  </StyledCard>
);

export default Card;
